// base64
import { base64ToPath } from './image-tools'
/**
 * base64
 * @param { String } path 
 * @returns 
 */
 export function base64ToPathFn(path) {
	let reg =
		/^\s*data:([a-z]+\/[a-z0-9-+.]+(;[a-z-]+=[a-z0-9-]+)?)?(;base64)?,([a-z0-9!$&',()*+;=\-._~:@\/?%\s]*?)\s*$/i
	if (!reg.test(path)) {
		return Promise.resolve(path)
	}
	return base64ToPath(path)
}

/**
 *  HTTP GET 
 * @param { String } url 
 * @param { Object } options  
 * @returns 
 */
export function downloadFile(url, options = {
	onProgressUpdate: () => {}
}) {
	return new Promise(resolve => {
		try {
			let download = uni.downloadFile({
				url,
				header: options.header || {},
				success(res) {
					return resolve({
						success: true,
						data: res
					})
				},
				fail() {
					return resolve({
						success: false,
						message: `${url}`
					})
				}
			})
			// 
			download.onProgressUpdate(data => {
				options.onProgressUpdate(data)
			})
		} catch(e) {
			return resolve({
				success: false,
				msg: `${url}`
			})
		}
	})
}

/**
 * 
 * @param { String } title 
 * @param { Boolean } mask 
 */
export function showLoading(title, mask = true) {
	uni.showLoading({
		title,
		mask
	})
}

/**
 * 
 */
export function hideLoading() {
	uni.hideLoading()
}

/**
 * 
 * @param { String } title 
 * @param { Object } options 
 * @param { Stirng } options.icon 
 * @param { String || Number } options.duration 
 */
export function showToast(title, options = {}) {
	uni.showToast({
		title,
		icon: options.icon || "none",
		duration: options.duration || 1500,
		mask: options.mask || false
	})
}

/**
 * 
 * @param { String } filePath 
 * @returns 
 */
export function saveImageToPhotosAlbum(filePath) {
	return new Promise(resolve => {
		showLoading('...')
		uni.saveImageToPhotosAlbum({
			filePath,
			success(res) {
				hideLoading()
				resolve({
					success: true,
					data: res.file
				})
			},
			fail(err) {
				hideLoading()
				resolve({
					success: false,
					message: err
				})
			}
		})
	})
}

/**
 * 
 * @param { CanvasText } Context canvas
 * @param { String } text 
 * @param { String } size 
 * @returns 
 */
export function countTextLength(Context, text, size) {
    let textLength = 0
    Context.setFontSize(size)
    try {
        textLength = Context.measureText(text)
    } catch(e) {
        textLength = {}
    }
    textLength = textLength && textLength.width ? textLength.width : 0
    if (textLength == 0) {
        for (let i of text) {
            textLength += Context.measureText(text)
        }
        textLength * size
    }
    return textLength
}


/**
 *  H5
 * @param { Object } params 
 * @param { String } params.src 
 * @param { String } params.quality 0100jpg
 * @param { String } params.width "100px""50%""auto"heightheight
 * @param { String } params.height "100px""50%""auto"heightheight
 * @returns 
 */
export function compressImage(params = {}) {
	return new Promise(resolve => {
		uni.compressImage({
			src: params.src || '',
			quality: params.quality || 80,
			width: params.width || 'auto',
			height: params.height || 'auto',
			success: res => {
				resolve({
					success: true,
					src: res.tempFilePath
				})
			},
			fail: res => {
				resolve({
					success: false,
					message: ''
				})
			}
		})
	})
}


