/**
 * 
 * @param { String | Number } oWidth 
 * @param { String | Number } oHeight 
 * @param { String | Number } x x
 * @param { String | Number } y y
 * @param { String | Number } width 
 * @param { String | Number } height 
 * @param { String } mode  
 * 			 		 aspectFit 
 * 					 aspectFill 
 * 					 widthFix  
 * 					 heightFix 
 */
export function getModeImage(oWidth, oHeight, x, y, width, height, mode) {
	if (mode == 'aspectFit') {
		return getAspectFitModelInfo(oWidth, oHeight, x, y, width, height)
	}

	if (mode == 'aspectFill') {
		return getAspectFillModelInfo(oWidth, oHeight, x, y, width, height)
	}

	if (mode == 'widthFix') {
		return getWidthFixModelInfo(oWidth, oHeight, x, y, width, height)
	}

	if (mode == 'heightFix') {
		return getHeightFixModelInfo(oWidth, oHeight, x, y, width, height)
	}
	
	if (mode == 'default') {
		return {
			dw: width,
			dh: height,
			dx: x,
			dy: y
		}
	}
	return getAspectFillModelInfo(oWidth, oHeight, x, y, width, height)
}


// aspectFit 
function getAspectFitModelInfo(oWidth, oHeight, x, y, width, height) {
	let aspect = oHeight / oWidth
	let sw = width
	let sh = aspect * sw
	if (aspect > 1) {
		aspect = oWidth / oHeight
		sh = height
		sw = aspect * sh
	}
	return {
		sw,
		sh,
		sx: x,
		sy: y,
		dw: oWidth,
		dh: oHeight,
		dx: 0,
		dy: 0
	}
}

// 
function getAspectFillModelInfo(oWidth, oHeight, x, y, width, height) {
	//  
	let aspect = oHeight / oWidth
	let sw = width
	let sh = aspect * sw
	let dx = 0
	let dy = (sh - height) / 2
	if (aspect < 1) {
		//  
		aspect = oWidth / oHeight
		sh = height
		sw = aspect * sh
		dy = 0
		dx = (sw - width) / 2
	}
	return {
		sw,
		sh,
		sx: x,
		sy: y,
		dw: oWidth,
		dh: oHeight,
		dx,
		dy
	}
}


// 
function getWidthFixModelInfo(oWidth, oHeight, x, y, width, height) {
	let aspect = oHeight / oWidth
	let sw = width
	let sh = sw * aspect
	let dx = 0
	let dy = 0
	return {
		sw,
		sh,
		sx: x,
		sy: y,
		dw: oWidth,
		dh: oHeight,
		dx,
		dy
	}
}


// 
function getHeightFixModelInfo(oWidth, oHeight, x, y, width, height) {
	let aspect = oWidth / oHeight
	let sh = height
	let sw = sh * aspect
	let dx = 0
	let dy = 0
	return {
		sw,
		sh,
		sx: x,
		sy: y,
		dw: oWidth,
		dh: oHeight,
		dx,
		dy
	}
}